import React, { Component } from 'react';
import 'antd/dist/antd.css';
import './AddTeacher.css';
import axios from 'axios';
import { navigate } from '@reach/router';
import { Card, Input, TimePicker, Menu, Dropdown, Typography, Button, Radio, message } from 'antd';
import { UserOutlined, VideoCameraOutlined, NumberOutlined, PushpinOutlined } from '@ant-design/icons';
const { Title } = Typography;

export class AddTeacher1 extends Component {
    constructor(props) {
        super(props)

        this.state = {
            teacherName: '',
            shortName: '',
            designation: ''
        }
    }

    render() {
        const { teacherName, shortName, designation } = this.state;
        return (
            <Card
                className="card"
                style={{ backgroundColor: "#F3F1FF", margin: "12px" }}
            >
                <Title className="input" level={3}>Add/Edit</Title>

                <Input className="input" size="large" placeholder="Teacher Name" prefix={<NumberOutlined />} value={teacherName} onChange={(e) => this.setState({ teacherName: e.target.value })} />
                <Input className="input" size="large" placeholder="Short Name" prefix={<NumberOutlined />} value={shortName} onChange={(e) => this.setState({ shortName: e.target.value })} />
                <Input className="input" size="large" placeholder="Designation" prefix={<NumberOutlined />} value={designation} onChange={(e) => this.setState({ designation: e.target.value })} />



                <Button
                    type="primary"
                    className="input"
                    style={{ backgroundColor: "#141414" }}
                    onClick={() => {
                        console.log(teacherName, shortName, designation);

                        // const requestOptions = {
                        //     method: 'GET',
                        //     headers: { 'Content-Type': 'application/json' },
                        //     // body: JSON.stringify({ teacherName: teacherName, shortName: shortName, designation: designation })
                        // };
                        // fetch('http://localhost:5000/api/teacher', requestOptions)
                        axios
                            .post(`http://localhost:5000/api/teacher`, {
                                teacherName: teacherName, shortName: shortName, designation: designation
                            })
                        this.setState({
                            teacherName: '',
                            shortName: '',
                            designation: ''
                        })
                        alert('Added Sucessfully')
                        navigate('/all');
                        // if (ValidateFields() === true) {
                        //     ListEdit.addLecture(uuid, subjectName, teacherName, classCode, classGroup, noOfPeriod, courseCode, link1, startTime, endTime, day, type)
                        //     navigate('/all');
                        // }
                        // else { }
                    }}>
                    Submit
                </Button>
            </Card>
        )
    }
}

export default AddTeacher1
